import { Component, OnInit } from '@angular/core';
import { NavController, NavParams } from '@ionic/angular';
import { ActivatedRoute } from '@angular/router';
import { MasterDetailService } from '../../app/services/masterdetail.service';

@Component({
  selector: 'app-gallery',
  templateUrl: './gallery.page.html',
  styleUrls: ['./gallery.page.scss'],
})
export class GalleryPage {


    public images: Array<string>;
    private grid: Array<Array<string>>;
    varComId;

    constructor(private navCtrl: NavController, private route: ActivatedRoute, private masterDetailService: MasterDetailService) {
        
        
         
    }

    goback() {
        this.navCtrl.goBack();
    }

    ngOnInit() {
        this.images = this.masterDetailService.getUris();
        this.grid = Array(Math.ceil(this.images.length / 2));
        let rowNum = 0;

        for (let i = 0; i < this.images.length; i += 2) {

            this.grid[rowNum] = Array(2);

            if (this.images[i]) {
                this.grid[rowNum][0] = this.images[i]
            }

            if (this.images[i + 1]) {
                this.grid[rowNum][1] = this.images[i + 1]
            }

            rowNum++;
        }

    }


}
